html,
body {
  height: 100%;

  font-family: system-ui, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto,
    Oxygen, Ubuntu, Cantarell, Droid Sans, Helvetica Neue, Arial, Helvetica,
    sans-serif;
  font-size: 14px;
  font-weight: 400;

  overflow: hidden;
}

#root {
  height: 100%;
}

.App {
  text-align: center;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.toolbar {
  background: #f0f0f0;
  padding: 4px 4px;
  border-bottom: 1px solid #d1d1d1;
  box-shadow: -1px 1px 2px rgba(0, 0, 0, 0.1);
}

.inner {
  display: flex;
}

button {
  cursor: pointer !important;
  background: transparent;
  transition: background-color 0.2s ease-in-out;
  padding: 0 5px;
  color: #626262;
  font-size: 21px;
  border-radius: 2px;
  border: 2px solid transparent;
  background-repeat: no-repeat;
  background-size: 70%;
  background-position: 50% 50%;
  text-indent: -9000px;

  cursor: pointer;

  height: 29px;
  width: 29px;

  outline: 0;
}

button:not([disabled]):active:hover {
  background-color: #d3d3d3;
}

button:not([disabled]):hover,
button:not([disabled]):active {
  background-color: #ddd;
}

button:disabled {
  opacity: 0.1;
}

button.active {
  background-color: #d5d5d5;
}

.urlbar {
  display: flex;
  flex: 1 1;
  background: #fff;
  border: 1px solid #ccc;
  border-radius: 4px;
  color: #888;
  height: 27px;
  overflow: hidden;
  margin-left: 4px;
  padding: 0 4px;
}

.urlbar input {
  width: 100%;
  border: 0;
  outline: 0;
  -webkit-user-select: all;
          user-select: all;
}

.focused {
  box-shadow: inset 0 0 0 0.3px #419bf9;
  border-color: #419bf9;
}

.contextMenu {
  display: flex;
  flex-direction: column;
  position: absolute;
  left: 0;
  top: 0;
  background: #fff;
  border: 1px solid #ccc;
  border-radius: 4px;
  color: #484848;
  padding: 0;
  margin: 0;
  z-index: 1100;
  width: 150px;
  list-style-type: none;
}

.contextMenuItem {
  border: 0;
  outline: 0;
  padding: 5px 10px 5px 10px;
  text-align: left;
  vertical-align: middle;
  cursor: default;
}

.contextMenuItem:hover {
  background-color: #ebebeb;
}

.disabled {
  pointer-events: none;
  opacity: 0.5;
}

.hidden {
  display: none;
}

.separator {
  background-color: #ccc;
  height: 1px;
  padding: 0;
  margin: 3px 0 3px 0;
}

.device-settings {
  display: none;
  border-top: 1px solid #d1d1d1;
}

.device-settings.active {
  display: block;
}

select {
  background: transparent;
  border: none;
  font-size: 11px;
  height: 29px;
  padding: 5px; /* If you add too much padding here, the options won't show in IE */
}

.viewport-size-input {
  max-width: 45px;
  text-align: center;
  background: #f0f0f0;
  box-shadow: none;
  border: 1px solid transparent;
  outline: 0;
}

.viewport-size-input:active,
.viewport-size-input:focus {
  border: 1px solid #d3d3d3;
  background: #fff;
}

.metadata .spacer {
  padding: 0 2px;
}

.viewport {
  display: flex;
  background: #fff;
  position: relative;
  overflow: hidden;
  box-sizing: border-box;
  height: 100%;
  width: 100%;

  justify-content: center;
  align-items: center;
}

.viewport-resizable-wrap {
  box-shadow: 0 0 300px -50px rgba(0, 0, 0, 0.59);
  border: 1px solid #d1d1d1;
}

.viewport-resizer {
  background: rgba(240, 240, 240, 1);
}

.viewport-resizer:hover {
  background-color: rgb(207, 207, 207);
}

.resizer-top {
  left: 0 !important;
  top: -10px !important;
}

.resizer-top:before {
  content: '';
  position: absolute;
  top: calc(50% + 1px);
  left: calc(50% - 12px);
  height: 2px;
  width: 24px;
  background: rgba(0, 0, 0, 0.4);
}

.resizer-top:after {
  content: '';
  position: absolute;
  top: calc(50% - 3px);
  left: calc(50% - 12px);
  height: 2px;
  width: 24px;
  background: rgba(0, 0, 0, 0.4);
}

.resizer-top-left {
  width: 10px !important;
  height: 10px !important;
  z-index: 2;
  left: -10px !important;
  top: -10px !important;
}

.resizer-top-left:before {
  content: '';
  position: absolute;
  left: 2px;
  bottom: 2px;
  width: 5px;
  height: 5px;
  border-left: 2px solid rgba(0, 0, 0, 0.4);
  border-top: 2px solid rgba(0, 0, 0, 0.4);
}

.resizer-top-right {
  width: 10px !important;
  height: 10px !important;
  z-index: 2;
  right: -10px !important;
  top: -10px !important;
}

.resizer-top-right:before {
  content: '';
  position: absolute;
  right: 2px;
  bottom: 2px;
  width: 5px;
  height: 5px;
  border-right: 2px solid rgba(0, 0, 0, 0.4);
  border-top: 2px solid rgba(0, 0, 0, 0.4);
}

.resizer-bottom:before {
  content: '';
  position: absolute;
  top: calc(50% + 1px);
  left: calc(50% - 12px);
  height: 2px;
  width: 24px;
  background: rgba(0, 0, 0, 0.4);
}

.resizer-bottom {
  bottom: -10px !important;
}

.resizer-bottom:after {
  content: '';
  position: absolute;
  top: calc(50% - 3px);
  left: calc(50% - 12px);
  height: 2px;
  width: 24px;
  background: rgba(0, 0, 0, 0.4);
}

.resizer-bottom-left {
  width: 10px !important;
  height: 10px !important;
  z-index: 2;
  left: -10px !important;
  bottom: -10px !important;
}

.resizer-bottom-left:before {
  content: '';
  position: absolute;
  left: 2px;
  bottom: 2px;
  width: 5px;
  height: 5px;
  border-left: 2px solid rgba(0, 0, 0, 0.4);
  border-bottom: 2px solid rgba(0, 0, 0, 0.4);
}

.resizer-bottom-right {
  width: 10px !important;
  height: 10px !important;
  z-index: 2;
  right: -10px !important;
  bottom: -10px !important;
}

.resizer-bottom-right:before {
  content: '';
  position: absolute;
  right: 2px;
  bottom: 2px;
  width: 5px;
  height: 5px;
  border-right: 2px solid rgba(0, 0, 0, 0.4);
  border-bottom: 2px solid rgba(0, 0, 0, 0.4);
}

.resizer-left {
  left: -10px !important;
}

.resizer-left:before {
  content: '';
  position: absolute;
  left: 2px;
  top: calc(50% - 12px);
  width: 2px;
  height: 24px;
  background: rgba(0, 0, 0, 0.4);
}

.resizer-left:after {
  content: '';
  position: absolute;
  left: 6px;
  top: calc(50% - 12px);
  width: 2px;
  height: 24px;
  background: rgba(0, 0, 0, 0.4);
}

.resizer-right {
  right: -10px !important;
}

.resizer-right:before {
  content: '';
  position: absolute;
  left: 2px;
  top: calc(50% - 12px);
  width: 2px;
  height: 24px;
  background: rgba(0, 0, 0, 0.4);
}

.resizer-right:after {
  content: '';
  position: absolute;
  left: 6px;
  top: calc(50% - 12px);
  width: 2px;
  height: 24px;
  background: rgba(0, 0, 0, 0.4);
}

.loading-bar {
  pointer-events: none;
  transition: 400ms linear all;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

.bar {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1000;
  display: none;
  width: 100%;
  height: 2px;
  background: #29d;
  border-radius: 0 1px 1px 0;
  transition: width 350ms;
}

.peg {
  position: absolute;
  top: 0;
  right: 0;
  width: 70px;
  height: 2px;
  border-radius: 50%;
  opacity: 0.45;
  box-shadow: #29d 1px 0 6px 1px;
}

.spinner {
  position: fixed;
  top: 5px;
  left: 5px;
  z-index: 1000;
  pointer-events: none;
  transition: 350ms linear all;
}

.screencast {
  height: 100%;
  width: 100%;
  image-rendering: -moz-crisp-edges;
  image-rendering: pixelated; /* Disables interpolation. See https://stackoverflow.com/questions/7615009/disable-interpolation-when-scaling-a-canvas */
}

.img-hidden {
  display: none;
}

.viewport-info {
  background: rgba(240, 240, 240, 0.6);
  padding: 6px;

  position: absolute;
  top: 0;
  right: 0;
  z-index: 10;

  font-size: 13px;
  color: #333;

  opacity: 1;
  transition: opacity 0.5s ease-in-out;
}

.hidden {
  opacity: 0;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

